import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';

// import { FilterHeaders, BreadcrumbHeaders } from "../../../@core/models";
import { TransactionService } from "../../../@core/services";
import { ToastrComponent } from '../../../@core/components';
import * as moment from 'moment'

@Component({
  selector: 'ngx-ipg-transaction-history',
  templateUrl: './ipg-transaction-history.component.html',
  styleUrls: ['./ipg-transaction-history.component.scss']
})
export class IpgTransactionHistoryComponent implements OnInit {
  // componentHeaders: FilterHeaders[];
  // breadcrumb: BreadcrumbHeaders[];
  // tableHeaders: any[];

  // pagination
  page: number = 1;
  page_size: number = 10;
  transactions_count: number = 0;

  is_loading_1: boolean = false;
  is_loading_2: Array<boolean> = Array(this.page_size).fill(false);
  is_loading_3: boolean = false;
  filter_form: FormGroup = null;
  refundform: FormGroup = null;
  transactions: any[] = [];
  view_transaction: any = null;
  modal_ref: any = null;
  modal_ref_refund
  userMessage
  item = true
  tran_id:any
  // is_partially = 0


  // filterCondition: string;
  
  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private transactionService: TransactionService,
    private toastr: ToastrComponent,
  ) { 
    this.filter_form = this.fb.group({
      transaction_id: [''],
      start_time: [''],
      end_time: [''],
      status: ['SUCCESS'],
      email: [''],
      phone_no: [''],
    });
    this.refundform = this.fb.group({
      is_partially: [0],
      transaction_id: [''],
      amount: [''],
    });
   
    
  }

  // filter form fields
  get transaction_id() {
    return this.filter_form.get('transaction_id');
  }

  get start_time() {
    return this.filter_form.get('start_time');
  }

  get end_time() {
    return this.filter_form.get('end_time');
  } 

  get status() {
    return this.filter_form.get('status');
  } 

  get email() {
    return this.filter_form.get('email');
  } 

  get phone_no() {
    return this.filter_form.get('phone_no');
  } 


  // get is_partially() {
  //   return this.refundform.get('is_partially');
  // }
  get transaction_id_p() {
    return this.refundform.get('transaction_id');
  }
  get amount() {
    return this.refundform.get('amount');
  }

  ngOnInit(): void {
    this.getIPGTransactions();
  }

  getIPGTransactions() {
    this.is_loading_1 = true;

    this.transactionService.getIPGTransactionList(
      this.page, this.page_size, this.transaction_id.value,this.start_time.value ? moment(this.start_time.value).format('YYYY-MM-DD HH:mm:ss') : '',this.end_time.value? moment(this.end_time.value).format('YYYY-MM-DD HH:mm:ss'):'' , this.status.value, this.email.value, this.phone_no.value
    ).then((res: any) => {
      console.log('getIPGTransactions', res);
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.transactions = res?.data['data'];
        this.transactions_count = res?.data?.limit;

      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings ipg transaction history failed.');
      }
    }, (error: any) => {
        this.is_loading_1 = false;
        console.log('Internal Server Error', error);
        this.toastr.showToast('danger', 'Error!', 'Internal Server Error.');
    });

  }

  getSingleTransaction(modal: TemplateRef<any>, id: number, index: number) {
    this.is_loading_2[index] = true;

    this.transactionService.getSingleTransaction(id).then((res: any) => {
      // console.log('getSingleTransaction', res);
      this.is_loading_2[index] = false;

      if (res?.status === 100) {
        this.view_transaction = res?.data;
        this.modal_ref = this.dialogService.open(modal, { closeOnBackdropClick: false });
      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings transaction details failed.');
      }
    }, (error: any) => {
      this.is_loading_2[index] = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.');
    });

  }

  pageChanged(newPage: number) {
    this.page = newPage;
    this.getIPGTransactions();
  }

  formatDate(datetimeString: string) {
    let date = datetimeString.split(' ')[0];
    date = date.split('-').reverse().join('/');
    return date;
  }

  clearFilterForm() {
    if(!this.transaction_id.value && !this.start_time.value && !this.end_time.value && !this.status.value && !this.email.value && !this.phone_no.value) {
      return;
    }

    this.filter_form.reset();
    this.page = 1;
    this.getIPGTransactions();

  }


  // updateTable(query): void {
  //   this.filterCondition = query;
  //   this.pageChanged(1);
  // }

  // pageChanged(newPage: number): void {
  //   this.p = newPage;
  //   this.requestData(newPage, this.page_size, this.filterCondition);
  // }

  // requestData(pageNumber, limit, condition): void {
  //   this.transactionService.getIPGTransactionList(pageNumber, limit, condition).then(res => {
  //     console.log(res)
  //   }, err => {

  //   });
  // }


  // refund
  isFieldInvalid(fieldName: string) {
    const fieldObj = this.refundform.get(fieldName);
    let is_error: boolean = false;
    
    if(fieldObj && (fieldObj?.dirty || fieldObj?.touched)) {  
      is_error = fieldObj?.invalid && fieldObj?.errors && true;
      
      if(fieldName === 'amount') {
       is_error = !this.checkAmount();
      }

    }

    return is_error;

  }
  checkAmount (){
    if (this.refundform.get('is_partially').value && !this.refundform.get('amount').value)  {
      return false;
    }
    // if(this.is_loading_3){
    //   return false;

    // }
    return true;
    
  }
  openRefund(refundModal: TemplateRef<any>, transaction) {
  
    this.tran_id = transaction.id
   
    this.modal_ref_refund = this.dialogService.open(refundModal, { closeOnBackdropClick: false });
  }

  refundData() {
    this.is_loading_3 = true
    let request = {
      "transaction_id": this.tran_id
    }
   
    if (this.refundform.get('is_partially').value == 0) {
      request["is_partially"] = 0
    }
    if(this.refundform.get('is_partially').value == 1) {
      request["is_partially"] = 1,
      request["amount"] = this.refundform.get('amount').value
    }

    
    
     
      if (!this.refundform.valid) {
        this.is_loading_3 = false
        this.toastr.showToast('danger', 'error', 'error')
      } else {
      this.transactionService.refundData(request).then(res => {
        // this.is_loading_3 = false
        if (res['status'] == 100) {
          this.item = false
          this.getIPGTransactions();
          this.modal_ref_refund.close();
          // this.refundform.reset()
          this.toastr.showToast("success", "Cheers!","Refund requested successfully!")
          this.is_loading_3 = false
        } else {
          console.log('error')
          this.userMessage = res['message']
          this.toastr.showToast("danger", "Oh Snap!", res["message"])
          this.is_loading_3 = false
        }
      }, error => {
        this.is_loading_3 = false
        console.log(error)
        this.toastr.showToast("danger", "Oh Snap!", "Internal Server Error!")
      })
    }
  }
  choose(event) {
   
    console.log(event)
    if (event == true) {
      this.item = false

    } else {
      this.item = true
    }
  }
  close(){
    this.modal_ref_refund.close();
    // this.refundform.reset()
  }

}
