import { Component, OnInit,TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { TransactionService } from "../../../@core/services";
import { ToastrComponent } from '../../../@core/components';
import * as moment from 'moment'

@Component({
  selector: 'ngx-qr-dynamic-transaction-history',
  templateUrl: './qr-dynamic-transaction-history.component.html',
  styleUrls: ['./qr-dynamic-transaction-history.component.scss']
})
export class QrDynamicTransactionHistoryComponent implements OnInit {
  page: number = 1;
  page_size: number = 10;
  transactions_count: number = 0;

  is_loading_1: boolean = false;
  is_loading_2: Array<boolean> = Array(this.page_size).fill(false);
  filter_form: FormGroup = null;
  transactions: any[] = [];
  ipg_details: any[] = [];
  view_transaction: any = null;
  modal_ref: any = null;

  start_date: any


  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private transactionService: TransactionService,
    private toastr: ToastrComponent,
  ) {
    this.filter_form = this.fb.group({
      onepay_transaction_id: [''],
      start_time: [''],
      end_time: [''],
      status: [''],
    });
  }
  // filter form fields
  get onepay_transaction_id() {
    return this.filter_form.get('onepay_transaction_id');
  }

  get start_time() {
    return this.filter_form.get('start_time');
    // return this.filter_form.get.moment(this.start_time).format('YYYY-MM-DD HH:mm:ss');
    // moment(start).format('YYYY-MM-DD HH:mm:ss')
    // return moment(this.filter_form.get('start_time').format('YYYY-MM-DD H:mm:ss'))
    // return this.start_date ==  this.filter_form.get(moment(this.start_time).format('YYYY-MM-DD H:mm:ss'),);
  }

  get end_time() {
    return this.filter_form.get('end_time');
  }

  get status() {
    return this.filter_form.get('status');
  }



  ngOnInit(): void {
    this.getIPGTransactions();
  }
  getIPGTransactions() {
    this.is_loading_1 = true;

    this.transactionService.getQrTransactionList(
      this.page, this.page_size, this.onepay_transaction_id.value,this.start_time.value ? moment(this.start_time.value).format('YYYY-MM-DD'):'',this.end_time.value ? moment(this.end_time.value).format('YYYY-MM-DD'):'' , this.status.value
    ).then((res: any) => {
      console.log(res)
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.transactions = res?.data['data'];
        this.ipg_details = res?.data['data']['ipg_details'];
        this.transactions_count = res?.data?.limit;
        console.log('getIPGTransactions333', this.transactions);

      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings ipg transaction history failed.');
      }
    }, (error: any) => {
      this.is_loading_1 = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.22');
    });

  }
  pageChanged(newPage: number) {
    this.page = newPage;
    this.getIPGTransactions();
  }

  clearFilterForm() {
    if (!this.onepay_transaction_id.value && !this.start_time.value && !this.end_time.value && !this.status.value) {
      return;
    }

    this.filter_form.reset();
    this.page = 1;
    this.getIPGTransactions();

  }
  
  // open
  
  openModal(modal: TemplateRef<any>, data: any = null) {
    if(data) {
      this.view_transaction = data;
    }
    this.modal_ref = this.dialogService.open(modal, { closeOnBackdropClick: false });

  }
}
