import { Component, OnInit,TemplateRef,ViewChild } from '@angular/core';
import { FormBuilder, FormGroup,Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { TransactionService } from "../../../@core/services";
import { ToastrComponent } from '../../../@core/components';
import { Router, ActivatedRoute } from '@angular/router';
import * as moment from 'moment'

@Component({
  selector: 'ngx-view-evet-transaction',
  templateUrl: './view-evet-transaction.component.html',
  styleUrls: ['./view-evet-transaction.component.scss']
})
export class ViewEvetTransactionComponent implements OnInit {
  
  page: number = 1;
  page_size: number = 10;
  count: number = 0;
  is_loading_1: boolean = false;
  is_loading_2: Array<boolean> = Array(this.page_size).fill(false);
  filter_form: FormGroup = null;
  event: any[] = [];
  resend_notification:any[] = []
  view_transaction:any[] = []
  id
  id2
  modal_ref_notification
  modal_ref: any = null;
  modal_ref_uncheck: any = null;
  uncheckEventForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private transactionService: TransactionService,
    private toastr: ToastrComponent,
    private router: Router,
    private route: ActivatedRoute,
  ) {
    this.filter_form = this.fb.group({
      ipg_transaction_id: [''],
      customer_email: [''],
      status: [''],
      start_date: [''],
      end_date: [''],
    });
    this.uncheckEventForm = this.fb.group({
      reason: ["", Validators.required],
    });
   }
   get ipg_transaction_id() {
    return this.filter_form.get('ipg_transaction_id');
  }
  get customer_email() {
    return this.filter_form.get('customer_email');
  }
  get status() {
    return this.filter_form.get('status');
  }
  get start_date() {
    return this.filter_form.get('start_date');
  }
  get end_date() {
    return this.filter_form.get('end_date');
  }

  ngOnInit(): void {
    this.getEventData()
    
  }
  
  getEventData() {
    

    this.is_loading_1 = true;

    this.transactionService.getEventHistory(
      this.page, this.page_size, this.route.snapshot.params.id,this.ipg_transaction_id.value,this.customer_email.value,this.status.value,this.start_date.value ? (moment(this.start_date.value).format('YYYY-MM-DD')) : '',this.end_date.value? (moment(this.end_date.value).format('YYYY-MM-DD')):''
    ).then((res: any) => {
      console.log(res,'evenr data view')
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.event = res?.data['data'];
        // this.user_id.setValue({
        //   user_id: res?.data['data'].onepay_transaction_id,
        // });
       
        this.count = res?.data?.count;
       
      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings event transaction history failed.');
      }
    }, (error: any) => {
      this.is_loading_1 = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.22');
    });

  }
  pageChanged(newPage: number) {
    this.page = newPage;
    this.getEventData();
  }

  clearFilterForm() {
    if (!this.ipg_transaction_id.value && !this.status.value &&
      !this.customer_email.value && !this.start_date.value && !this.end_date.value) {
      return;
    }

    this.filter_form.reset();
    this.page = 1;
    this.getEventData();

  }
  formatDate(datetimeString: string) {
    let date = datetimeString.split(' ')[0];
    date = date.split('-').reverse().join('/');
    return date;
  }

   // open
  
   openModal(modal: TemplateRef<any>, data: any = null) {
    if(data) {
      this.view_transaction = data;
      console.log(this.view_transaction,'data view')
    }
    this.modal_ref = this.dialogService.open(modal, { closeOnBackdropClick: false });

  }
  // uncheck event transaction
   // open
  
   openUncheckModal(uncheckEvent: TemplateRef<any>, data: any = null) {
   
    if(data) {
      this.id = data;
      console.log(this.id,'onepay transaction')
    }
    this.modal_ref_uncheck = this.dialogService.open(uncheckEvent, { closeOnBackdropClick: false });

  }

  closeUncheck() {
    this.modal_ref_uncheck.close()
    this.uncheckEventForm.reset()
  }
  addUncheckData() {
    this.is_loading_1 = true
    let request = {
      "onepay_transaction_id":this.id,
      "reason" :this.uncheckEventForm.get('reason').value
    }
  console.log(request,'reason')
    this.transactionService.uncheckEventData(request).then(res => {
      this.is_loading_1 = false
    
      
      if (res['status'] == 100) {
        this.closeUncheck()
        this.getEventData()
        this.toastr.showToast('success', 'Success', 'Unchecked Successfully')
      } else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Failed.');
      }
    }, error => {
      this.is_loading_1 = false
      console.log(error)
      this.toastr.showToast('danger', 'Error', 'Internal server error')
    })
  }
  // resend notification
  notificationClose(){
    this.modal_ref_notification.close()
  }
  openNotification(Notification: TemplateRef<any>, data: any = null) {
    
    if(data) {
      this.id2 = data;
      console.log(this.id2,'onepay transaction')
    }
    this.modal_ref_notification = this.dialogService.open(Notification, { closeOnBackdropClick: false });

  }
  activeNotification() {
    this.is_loading_1 = true;

    this.transactionService.getNotification(
      this.id2
    ).then((res: any) => {
      console.log(res,'notification')
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.toastr.showToast('success', 'Success', 'Notification sent successfully')
        this.modal_ref_notification.close()

      
      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings notification data failed.');
      }
    }, (error: any) => {
      this.is_loading_1 = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.22');
    });

  }
}
