import { Component, OnInit, ViewChild, ElementRef, ChangeDetectorRef, OnDestroy, TemplateRef, Directive } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormControl } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { ToastrComponent } from '../../../../@core/components';

@Component({
  selector: 'ngx-view-transaction-new',
  templateUrl: './view-transaction-new.component.html',
  styleUrls: ['./view-transaction-new.component.scss']
})
export class ViewTransactionNewComponent implements OnInit {
  addNoteform: FormGroup;
  refundform:FormGroup
  modal_ref
  active: boolean = true;
  deactive: boolean = false;
  showAuthentication: boolean = false;

  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private toastr: ToastrComponent,
    
  ) { 
    this.addNoteform = this.fb.group({
      note: ['', [Validators.required]],
    });
    this.refundform = this.fb.group({
      refund_amount: ['', [Validators.required]],
      refund_reason: ['', [Validators.required]],
      refund_note: ['', [Validators.required]],
    });
  }

  ngOnInit(): void {
  }
  openAddNote(fromAddNoteModel: TemplateRef<any>) {
    this.modal_ref = this.dialogService.open(fromAddNoteModel, { closeOnBackdropClick: false });
  }
  cancel() {
    // this.fromAddressform.reset();
    this.modal_ref.close();
  }
  addNote(){

  }



  openRefund(fromRefundeModel: TemplateRef<any>) {
    this.modal_ref = this.dialogService.open(fromRefundeModel, { closeOnBackdropClick: false });
  }
  cancelRefund() {
    // this.fromAddressform.reset();
    this.modal_ref.close();
  }
  refund(){

  }

  stepperChange(data:any){
    console.log('data',data)
    if(data=="active"){
      this.active = true
      this.deactive = false
    }
    else if(data=="deactive"){
      this.active = false
      this.deactive = true

    }
  }

}
