import { Component, OnInit,TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { TransactionService } from "../../../@core/services";
import { ToastrComponent } from '../../../@core/components';

@Component({
  selector: 'ngx-event-history',
  templateUrl: './event-history.component.html',
  styleUrls: ['./event-history.component.scss']
})
export class EventHistoryComponent implements OnInit {
  page: number = 1;
  page_size: number = 10;
  count: number = 0;
  is_loading_1: boolean = false;
  is_loading_2: Array<boolean> = Array(this.page_size).fill(false);
  filter_form: FormGroup = null;
  all_event: any[] = [];
  view_event: any[] = [];
  modal_ref: any = null;
 


  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private transactionService: TransactionService,
    private toastr: ToastrComponent,
  ) {
    this.filter_form = this.fb.group({
      event_id: [''],
    });
   }
   get event_id() {
    return this.filter_form.get('event_id');
  }

  ngOnInit(): void {
    this.getEventData()
  }
  getEventData() {
    this.is_loading_1 = true;

    this.transactionService.getEvent(
      this.page, this.page_size, this.event_id.value 
    ).then((res: any) => {
      console.log(res,'evenr data')
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.all_event = res?.data['data'];
        this.count = res?.data?.count;

      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings event details failed.');
      }
    }, (error: any) => {
      this.is_loading_1 = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.22');
    });

  }
  pageChanged(newPage: number) {
    this.page = newPage;
    this.getEventData();
  }

  clearFilterForm() {
    if (!this.event_id.value) {
      return;
    }

    this.filter_form.reset();
    this.page = 1;
    this.getEventData();

  }
   // open
  
   openModal(modal: TemplateRef<any>, event: any = null) {
    if(event) {
      this.view_event = event;
    }
    this.modal_ref = this.dialogService.open(modal, { closeOnBackdropClick: false });

  }

}
