import { Component, OnInit,TemplateRef} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { TransactionService } from "../../../@core/services";
import { ToastrComponent } from '../../../@core/components';

@Component({
  selector: 'ngx-refund-history',
  templateUrl: './refund-history.component.html',
  styleUrls: ['./refund-history.component.scss']
})
export class RefundHistoryComponent implements OnInit {
  page: number = 1;
  page_size: number = 10;
  transactions_count: number = 0;

  is_loading_1: boolean = false;
  filter_form: FormGroup = null;
  transactions: any[] = [];
  ipg_details: any[] = [];
  view_transaction: any = null;
  modal_ref: any = null;

  constructor(
    private fb: FormBuilder,
    private dialogService: NbDialogService,
    private transactionService: TransactionService,
    private toastr: ToastrComponent,
  ) {
    this.filter_form = this.fb.group({
      onepay_transaction_id: [''],
      ipg_transaction_id: [''],
      is_partially: [''],
      status: [''],
      id: [''],
    });
   }
   get onepay_transaction_id() {
    return this.filter_form.get('onepay_transaction_id');
  }
  get ipg_transaction_id() {
    return this.filter_form.get('ipg_transaction_id');
  }
  get is_partially() {
    return this.filter_form.get('is_partially');
  }
  get status() {
    return this.filter_form.get('status');
  }
  get id() {
    return this.filter_form.get('id');
  }

  ngOnInit(): void {
    this.getrefund();
  }
  getrefund() {
    this.is_loading_1 = true;

    this.transactionService.getRefundHistory(
      this.page, this.page_size, this.ipg_transaction_id.value,this.onepay_transaction_id.value,this.status.value,this.is_partially.value,this.id.value
    ).then((res: any) => {
      console.log(res)
      this.is_loading_1 = false;

      if (res?.status === 100) {
        this.transactions = res?.data;
        this.transactions_count = res?.count;
        console.log('getIPGTransactions33344444444444444444444444444444444444444444444444444444444444', this.transactions);

      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Gettings ipg transaction history failed.');
      }
    }, (error: any) => {
      this.is_loading_1 = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.22');
    });

  }
  pageChanged(newPage: number) {
    this.page = newPage;
    this.getrefund();
  }

  clearFilterForm() {
    if (!this.ipg_transaction_id.value && !this.onepay_transaction_id.value && !this.status.value && !this.is_partially.value  && !this.id.value) {
      return;
    }

    this.filter_form.reset();
    this.page = 1;
    this.getrefund();

  }
   // open
  
   openModal(viewTransactionModal: TemplateRef<any>, data) {
    if(data) {
      this.view_transaction = data;
      console.log(this.view_transaction)
    }
    this.modal_ref = this.dialogService.open(viewTransactionModal, { closeOnBackdropClick: false });

  }

}
