import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { TransactionHistoryComponent } from './transaction-history.component';
import { IpgTransactionHistoryComponent } from './ipg-transaction-history/ipg-transaction-history.component';
import { QrDynamicTransactionHistoryComponent } from './qr-dynamic-transaction-history/qr-dynamic-transaction-history.component';
import { EventHistoryComponent } from  './event-history/event-history.component';
import { ViewEvetTransactionComponent} from './view-evet-transaction/view-evet-transaction.component';
import { RefundHistoryComponent} from './refund-history/refund-history.component';
import { IpgTransactionComponent} from './ipg-transaction/all-ipg-transaction/ipg-transaction/ipg-transaction.component'
import { ViewTransactionComponent} from './ipg-transaction/view-ipg-transaction/view-transaction/view-transaction.component'
import { TRANSACTION_HISTORY } from '../../../constant';
import { PrivillageAccess } from '../../@core/services/_privillageguard.service';
import { SettlementReportComponent } from './settlement-report/settlement-report.component'
import { ViewTransactionNewComponent} from './ipg-transaction/view-transaction-new/view-transaction-new.component'


const routes: Routes = [
  {
    path: '',
    component: TransactionHistoryComponent,
    children: [
      {
        path: 'ipg',
        component: IpgTransactionHistoryComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
        
      },
      {
        path: 'settlement-report',
        component: SettlementReportComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
        
      },
      {
        path: 'dynamic-qr',
        component: QrDynamicTransactionHistoryComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'event',
        component: EventHistoryComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'event-history/:id',
        component: ViewEvetTransactionComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'refund-history',
        component: RefundHistoryComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'ipg-transaction',
        component: IpgTransactionComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'view-ipg-transaction/:id',
        component: ViewTransactionComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
      {
        path: 'view-ipg-transaction-new',
        component: ViewTransactionNewComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: TRANSACTION_HISTORY,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TransactionHistoryRoutingModule {
}
